html, body {
  height: 100vh;
  min-width: 320px; }

body {
  margin: 0;
  background-color: #52ad7bbb;
  font-family: 'Raleway', sans-serif; }

a, a:link, a:visited, a:focus {
  text-decoration: none;
  outline: none;
  color: inherit; }

button, a, div {
  transition: all 0.3s ease-in-out; }

div, h2, h3, h4, h5, h6 {
  color: inherit;
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

.app {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-evenly; }

.slime-img {
  max-height: 300px;
  max-width: 400px; }

img {
  max-width: 100%;
  max-height: 100%; }

.slime-heading {
  color: white;
  text-align: center; }

.r {
  color: red; }

.app-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 100%;
  justify-content: space-between; }

.app-content {
  width: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  padding: 20px 0; }

.discord-link {
  padding: 10px 20px;
  color: #2C2F33;
  border-radius: 5px;
  border: 1px solid; }
  .discord-link:hover {
    background: #7289DA;
    color: white; }

